// COMMON 
*
{
    font-family: sans-serif;
    font-size: 15px;
    text-decoration: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    line-height: 1.5;
}
section
{
    padding: 80px 20px;
    height: 100vh;
    background: url(./img/bg.jpg) no-repeat;
    background-size: cover;
}
.inner
{
    max-width: 960px;
    margin: 0 auto;
    position: relative;
    height: 100%;
}
button
{
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
}

// NAV 
.nav 
{
    width: 100%;
    position: fixed;
    top: 0;
    background: rgba($color: #333, $alpha: 0.8);
    padding: 20px 60px;
    .inner 
    {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .nav__toggler 
        {
            display: none;
            width: 30px;
            height: 30px;
            background: transparent;
            border: none;
            outline: none;
            span 
            {
                display: block;
                width: 30px;
                height: 3px;
                margin: 6px 0;
                background: #fff;
                position: relative;
                transition: 0.4s linear;
            }
        }
        .nav__toggler.active
        {
            span:nth-child(1)
            {
                top: 4px;
                transform: rotate(-45deg);
            }
            span:nth-child(2)
            {
                opacity: 0;
            }
            span:nth-child(3)
            {
                bottom: 14px;
                transform: rotate(45deg);
            }
        }
        ul
        {
            display: flex;
            li
            {
                color: #fff;
                font-weight: 700;
                margin-right: 10px;
                padding: 10px 10px;
                list-style: none;
                cursor: pointer;
            }
        }
    }
}


@media screen and (max-width: 960px) 
{
    .nav
    {
        z-index: 999;
        background: transparent;
        padding: 20px 20px;
        .inner
        {
            max-width: 800px;
            .nav__toggler
            {
                display: block;
                z-index: 999;
            }
            .nav__items
            {
                position: fixed;
                top: 0;
                right: 0;
                max-width: 400px;
                width: 100%;
                height: 100vh;
                transform: translateX(100%);
                padding: 80px 40px;
                background: rgba($color: #333, $alpha: 0.8);
                transition: 0.3s linear;
                ul
                {
                    display: block;
                    li
                    {
                        position: relative;
                        margin: 30px 0;
                        font-size: 30px;
                        color: #eee;
                        transition: 0.4s linear;
                    }
                    li:hover::before
                    {
                        content: "";
                        width: 130px;
                        height: 2px;
                        background: #eee;
                        position: absolute;
                        bottom: 0;
                    }
                }
            }
            .nav__items.active
            {
                transform: translateX(0);
            }
        } 
    }
}

